<template>
  <div id="editor">

  </div>
</template>

<script>
  import WangEditor from 'wangeditor' //wangeditor
  import axios from "axios"
  export default {
    name: "editor",
    data() {
      return {
        editor:null,   //editor
        token:"",
        url:""
      }
    },
    props:{   //value
      value:{
        type:String
      }
    },
    methods:{
      getToken() {
        this.$axios.qiniuGet().then(res => {
          this.token = res.data
        })
      }
    },
    created() {  //  
      this.getToken()
      let that = this;  //functionthisthat
      this.$nextTick(() => {    //$nextTick  DOM 
                      // $nextTick DOM
        this.editor = new WangEditor("#editor");   //
        this.editor.customConfig.onchange = function (html) { //
        // html 
          // console.log(html);
          that.$emit('input',html);
          that.$emit('getTxt',that.editor.txt.text())
        }
        this.editor.customConfig.customUploadImg = function (files, insert) {
          // files  input 
          // console.log(files)
          var form = new FormData();
          form.append("file",files[0]);
          form.append("token",that.token);
          // console.log(that.token)
          axios.post("https://upload-z1.qiniup.com",form).then(res => {
            // console.log(res.data)
            that.url = "http://image.yaojunrong.com/" + res.data.key;
            // console.log(that.url)
            insert(that.url)
          })
          // insert  url 
          // 
        }
        this.editor.create()
        document.getElementById('editor').addEventListener('click', function () {
            //  editor.customConfig.onchange editor.change  undefined
            that.editor.change && that.editor.change()
        })
      })
    },
    watch:{// Valuehtml
      value(val){
        this.editor.txt.html(val)
      }
    }
  }
</script>

<style scoped>

</style>



